oca en la creación de empresas libres de consumo de sustancias psicoactivas, destacando la relevancia de establecer políticas y prácticas organizacionales que promuevan la salud y seguridad de todos los empleados.")])])])])])])]),_c('div',{staticClass:"row bg-gris bg-full-width p-4 py-xl-0"},[_c('div',{staticClass:"col-4 d-none d-xl-block"},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-6.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-8 d-flex"},[_c('p',{staticClass:"my-auto text-white"},[_vm._v("Al finalizar este programa formativo, los estudiantes estarán equipados no solo para identificar y gestionar los riesgos asociados al consumo de sustancias psicoactivas en sus organizaciones, sino también para desarrollar e implementar estrategias de prevención que favorezcan un entorno laboral proactivo y saludable. Esta formación les permitirá liderar iniciativas que no solo minimicen los riesgos, sino que también fomenten una cultura de bienestar y responsabilidad. En última instancia, este curso aspira a formar profesionales que comprendan la complejidad de esta problemática y se conviertan en agentes de cambio positivos en sus entornos laborales, impulsando la salud y la seguridad en sus organizaciones.")])])])])}]

